@charset "utf-8";

div.App {
  width: 800px; height: 100%;
  text-align: center; margin: 0 auto;
  div.box {
    background: lightgray;  padding: 30px;
  }
  figure {
    width: 800px; margin: 0 auto;
    > img {
      width: 100%; height: auto; display: block;
    }
    div.bot {
      width: 800px; height: 200px; display: flex; justify-content: space-between;
      img {
        width: 400px; height: 200px; display: block;
      }
    }
  }
}